                  <h4>How Staking Your Reaperville NFTs Works</h4>
                  <ul>
                    <li>
                      Normally, when you stake one or multiple NFTs, you
                      transfer the ownership of your NFTs to a smart contract
                      which promises to transfer them back when you unstake.
                    </li>
                    <li>
                      We at Surviville do things differently. When you stake a
                      Reaperville NFT, it remains in your wallet. However, you
                      cannot transfer a staked NFT to another wallet.
                    </li>
                    <li>
                      To be able to transfer a staked NFT, you have to unstake
                      it first -- which you can do at any time.
                    </li>
                  </ul>
                  <br />
                  <h4>Advantages and Disadvantages</h4>
                  <h6>Advantages</h6>
                  <ul>
                    <li>
                      Super low gas fees for staking and unstaking your NFTs.
                    </li>
                    <li>
                      The staked NFTs remain in your wallet. No need to trust
                      another smart contract with the custody of your staked
                      NFTs.
                    </li>
                  </ul>
                  <br />
                  <h6>Disadvantages</h6>
                  <ul>
                    <li>
                      Because the staked NFTs remain in your wallet, it may
                      appear as if you could transfer them. However, they can
                      only be transferred once you unstake them.
                    </li>
                    <li>
                      Note that third party DAPPs may not be aware of this
                      limitation. Attempted transfers of staked NFTs will fail
                      until you unstake the corresponding NFTs first with this
                      DAPP.
                    </li>
                  </ul>
