<template>
  <section
    class="text-center container col-lg-8 col-md-9 col-sm-10 mx-auto mt-2 mb-4"
  >
    <div class="row py-3">
      <div>
        <h1 class="fw-light">
          <strong><span>The Reaperville NFT Collection</span></strong>
        </h1>

        <div class="lead text-muted">
          <span
            >This collection of colorful reapers is the first NFT collection of
            the Surviville project. After minting some Reaperville NFTs, you can
            stake them
            <a target="_blank" href="https://surviville.github.io/reaper"
              >here</a
            >
            to earn a passive income of Surviville coins. These coins can be
            used to upgrade the level of your NFTs in order to increase its
            staking power. In the future, more use cases will follow such as
            giving your NFTs custom names and participating in winning
            opportunities.
          </span>
        </div>

        <div class="lead text-muted">
          <span>For {{ bchPerNft / 10 ** 18 }} BCH you can mint 1 NFT. </span>
        </div>

        <h4 class="lead text-muted mx-auto my-3">
          <span
            >You can mint up to 40 NFTs per transaction. The NFTs are minted in
            a non-sequential, randomised order.</span
          >
        </h4>

        <h4 class="lead text-muted mx-auto my-3">
          <span>Please select how many Reaperville NFTs you want to mint.</span>
        </h4>

        <h4 class="lead mx-auto my-4">
          <span>Note that the transaction cannot be undone.</span>
        </h4>
      </div>

      <div class="mb-3 mx-auto">
        <div class="input-group mx-auto">
          <div class="input-group-prepend mx-auto">
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="nftsToMint = 1"
            >
              1
            </button>
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="nftsToMint = 5"
            >
              5
            </button>
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="nftsToMint = 10"
            >
              10
            </button>
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="nftsToMint = 15"
            >
              15
            </button>
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="nftsToMint = 20"
            >
              20
            </button>
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="nftsToMint = 25"
            >
              25
            </button>
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="nftsToMint = 30"
            >
              30
            </button>
            <button
              class="btn btn-outline-secondary"
              type="button"
              @click="nftsToMint = 40"
            >
              40
            </button>
          </div>
        </div>
        <div class="col-lg-4 col-md-6 col-sm-8 mx-auto">
          <input
            id="tokensToBuy"
            type="number"
            min="1"
            :max="maxNftsPerTx"
            class="form-control mx-auto"
            @change="checkNftsToMint"
            v-model="nftsToMint"
          />
        </div>
      </div>

      <span class="row">
        <span class="column">
          <h4>Mint {{ nftsToMint }} Reaperville for:</h4>
          <div class="col-lg-6 col-md-8 mx-auto">
            <button
              id="buyButtonBch"
              type="button"
              class="btn btn-primary my-2 btn-large"
              :disabled="mintPaused || bchPerNft <= 0"
              @click="mintNFTsWithBCH"
            >
              {{ Math.round((nftsToMint * bchPerNft) / 10 ** 12) / 10 ** 6 }}
              BCH
            </button>
            <!-- &nbsp;
            <button
              id="buyButtonWom"
              type="button"
              class="btn btn-primary my-2 btn-large"
              :disabled="mintPaused || powTokenPriceWom <= 0"
              @click="buyPowWithToken('WoM')"
            >
              {{
                Math.round((nftsToMint * powTokenPriceWom) / 10 ** 12) / 10 ** 6
              }}
              WoM
            </button>
            &nbsp;
            <button
              id="buyButtonFlexUsd"
              type="button"
              class="btn btn-primary my-2 btn-large"
              :disabled="mintPaused || powTokenPriceFlexUsd <= 0"
              @click="buyPowWithToken('flexUSD')"
            >
              {{
                Math.round((nftsToMint * powTokenPriceFlexUsd) / 10 ** 12) /
                10 ** 6
              }}
              flexUSD
            </button> -->
          </div>
          <div class="lead text-muted">
            <span
              >Your Balances:
              {{ Math.round(stakersBchBalance / 10 ** 15) / 10 ** 3 }} BCH
              <!-- {{ Math.round(stakersWomBalance / 10 ** 15) / 10 ** 3 }} WoM,
              {{ Math.round(stakersFlexUsdBalance / 10 ** 15) / 10 ** 3 }}
              flexUSD -->
            </span>
          </div>

          <h4 class="lead text-muted mx-auto my-3">
            <span
              >The gas limit is set to {{ gasLimit }} to ensure a successful
              mint. Unused gas is automatically returned to your wallet. If you
              lower the gas limit, you risk failed transactions.</span
            >
          </h4>
        </span>
      </span>
    </div>

    <div class="row">
      <div
        class="text-monospace text-muted col-lg-12 col-md-12 mx-auto mt-1 mb-2"
      >
        {{ feedbackMessage }}
      </div>
    </div>

    <div class="row" v-if="txInProgress">
      <div
        class="text-monospace text-muted col-lg-12 col-md-12 mx-auto mt-1 mb-2"
      >
        Please wait for the transaction to finish!
      </div>
    </div>

    <!-- <br />
    <h1 class="fw-light"><strong>Your POW tokens</strong></h1>

    <div class="lead text-muted">
      <span>Balance: {{ stakersRewardTokenBalance }} POW </span>
    </div>
    <div
      class="lead text-muted"
      v-if="stakersRewardTokenBalance > 0 && stakersPowAllowanceForLeveling <= 0"
    >
      <span>Remember to approve the POW token for level upgrades.</span>
    </div>
    <div
      class="lead text-muted"
      v-if="stakersRewardTokenBalance > 0 && stakersPowAllowanceForLeveling > 0"
    >
      <span>You are now ready to level up some NFTs!</span>
    </div>

    <br /> 
    <div class="row py-5">
      <div>
        <h1 class="fw-light">
          <strong>POW Tokens Approval for Leveling</strong>
        </h1>

        <div class="lead text-muted">
          <span
            >Before you can upgrade the staking power of the NFTs, you have to
            approve the leveler contract as a spender of POW tokens.</span
          >
        </div>

        <div class="lead text-muted">
          <span>You only need to give your approval once.</span>
        </div>

        <div class="lead text-muted">
          <span
            >If you wish to remove your approval, you can do that here,
            too.</span
          >
        </div>

        <div class="col-12">
          <button
            class="btn btn-success"
            type="button"
            id="approvePowToken"
            :disabled="stakersPowAllowanceForLeveling > 0"
            @click="approvePowToken"
          >
            approve
          </button>
          &nbsp;
          <button
            class="btn btn-danger"
            type="button"
            id="removePowAllowance"
            :disabled="stakersPowAllowanceForLeveling <= 0"
            @click="removePowTokenAllowance"
          >
            (remove approval)
          </button>
        </div> 
      </div> 
    </div> -->
  </section>
</template>

<script>
import { ethers } from "ethers";
import Reaperville from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";

export default {
  name: "App",
  components: {},
  data() {
    return {
      provider: null,
      signer: null,

      stakersAddress: "",
      stakersBchBalance: 0,

      feedbackMessage: "",

      mintContract: null,
      mintPaused: true,
      nftsToMint: 10,
      maxNftsPerTx: 40,
      bchPerNft: 0,
      nftsMinted: 0,
      nftsToMintTotal: 2500,
      txInProgress: false,
      gasLimit: 250000,

      // stakersRewardTokenBalance: 0,

      // rewardTokenPerNft: 0,
      // flexUsdPerNft: 0,

      // rewardTokenContract: null,
      // flexUsdTokenContract: null,
    };
  },
  methods: {
    getBchBalance: function () {
      this.provider.getBalance(this.stakersAddress).then((balance) => {
        this.stakersBchBalance = balance;
      });
    },

    checkNftsToMint: function () {
      this.nftsToMint = Math.floor(this.nftsToMint);
    },

    mintNFTsWithBCH: async function () {
      await this.load();

      let nftsLeft = this.nftsToMintTotal - this.nftsMinted;

      if (this.nftsToMint > nftsLeft) {
        this.feedbackMessage = `There are only ${nftsLeft} NFTs available for minting!`;
        this.maxNftsPerTx = nftsLeft;
        return false;
      }

      let totalCostBchWei = this.bchPerNft * this.nftsToMint;
      let totalGasLimit = Math.min(
        10000000,
        this.gasLimit + (this.gasLimit - 25000) * (this.nftsToMint - 1)
      );

      console.log("totalCostBchWei: ", totalCostBchWei);
      console.log("Gas limit: ", totalGasLimit);

      if (this.stakersBchBalance < totalCostBchWei + totalGasLimit) {
        this.feedbackMessage = `You have ${
          Math.round(this.stakersBchBalance / 10 ** 14) / 10 ** 4
        } BCH in your wallet. You can mint up to ${Math.round(
          this.stakersBchBalance / Number(this.bchPerNft) - 0.5
        )} NFTs with that.`;
        return false;
      }

      this.feedbackMessage = `Minting your NFTs.`;
      this.txInProgress = true;

      let tx = await this.mintContract.mint(this.nftsToMint, {
        value: totalCostBchWei.toString(),
        gasLimit: totalGasLimit.toString(),
        gasPrice: 1046740000,
      });
      await tx.wait();
      console.log(tx);
      // .once("error", (err) => {
      //       console.log(err);
      //       this.feedbackMessage = "An error occured.\nPlease try again later.";
      //       this.txInProgress = false;
      //     })
      // .then((receipt) => {
      //   console.log(receipt);
      //   this.feedbackMessage = `Congratulations! You successfully minted some NFTs! Visit the staking website to take a look at your NFTs.`;
      //   this.txInProgress = false;
      // });

      this.feedbackMessage = `Congratulations! You successfully minted some NFTs! Visit the staking website to take a look at your NFTs.`;
      this.txInProgress = false;
    },

    mintReaper: async function (amount) {
      let bchAmount = this.bchPerNft * amount;
      if (bchAmount > this.stakersBchBalance * 10 ** 15) {
        this.feedbackMessage = "Not enough BCH!";
        return false;
      }
      this.feedbackMessage = "Minting NFTs. Please wait!";
      let c = new ethers.Contract(
        window.ReaperAddress,
        Reaperville.abi,
        this.provider.getSigner()
      );
      let tx = await c.mint(amount, {
        value: bchAmount.toString(),
        gasLimit: 9999999,
        gasPrice: 1046740000,
      });
      await tx.wait();
      window.location.reload();
    },

    mintWithBch: async function () {
      let powTokensInt = Math.floor(this.nftsToMint);
      if (powTokensInt !== this.nftsToMint) {
        this.feedbackMessage = "Only integers allowed!";
        this.nftsToMint = powTokensInt;
        return false;
      }
      if (this.nftsToMint < 10) {
        this.feedbackMessage = "You need to buy at least 10 POW tokens";
        return false;
      }

      let bchValue = (this.nftsToMint * this.bchPerNft) / 10 ** 18;
      if (bchValue > this.stakersBchBalance / 10 ** 18) {
        this.feedbackMessage = "Not enough BCH";
        return false;
      }

      if (bchValue <= 0) {
        this.feedbackMessage = "BCH payments are currently not accepted";
        return false;
      }

      this.feedbackMessage = "Buying " + this.nftsToMint + " POW tokens";
      const options = { value: ethers.utils.parseEther(bchValue.toString()) };
      let tx = await this.powSellerContract.buyPowWithBch(
        this.nftsToMint,
        options
      );
      await tx.wait();
      this.getPowTokenBalance();
      this.feedbackMessage = "";
    },

    load: async function () {
      this.signer.getAddress().then((myAddress) => {
        this.stakersAddress = myAddress;
        this.getBchBalance();
        // this.getRewardTokenBalance();
        // this.getFlexUsdBalance();
      });
      this.mintContract.isMintPausedStakingPaused().then((result) => {
        this.mintPaused = result[0];
      });
      this.mintContract.bchPerNft().then((_bchPerNft) => {
        this.bchPerNft = _bchPerNft;
      });
      this.mintContract.totalSupply().then((_nftsMinted) => {
        this.nftsMinted = _nftsMinted;
      });
    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();

        this.mintContract = new ethers.Contract(
          window.ReaperAddress,
          Reaperville.abi,
          this.provider.getSigner()
        );
      })
      .then(() => {
        this.load();
      });
  },
};
</script>